import xbbFunction from './xbb-function'

export const missionType = {
  promote: 3, // 
  view: 5, // 
  finish: 6, // 
  guest: 7 // 
}

//  iframe  message
export function xbbLogin () {
  // WEB
  try {
    parent.postMessage({ event: 'EVENT_THIRDPARTY_LOGIN' }, '*')
  } catch (err) {
    console.error(`web: ${err}`)
  }

  // iOS, Android
  try {
    xbbFunction.login()
  } catch (err) {
    console.error(`iOS and Android: ${err}`)
  }
}



