import { normalRequest } from '@/utils/request'
export function imgToBase64 (url, type) {
  return normalRequest({
    url: url,
    method: 'get'
  }).then(data => {
    return `data:${type};base64,${data.data.slice(32)}`
  }).catch(() => {
    //  img src ,  catch error
    return ''
  })
}



